const validation = () => {
  const arr = []
  const obj = {}
  arr.push(yup.string().optional())
  for (let i = 1; i < 76; i++) {
    arr.push(obj[i] = yup.string().required('   '))
  }
  return arr
}

const schema = yup.object().shape({
  ...validation()
})

const labels = [
  ' ',
  ' ',
  '',
  ' ',
  '',
  '',
  '',
  ' ',
]

